"use strict";

import type Biome from "../environment/biomes/biome.js";
import BiomeGenerator from "../environment/biomes/generator.js";
import BiomeGeneratorConfig from "../environment/biomes/generatorconfig.js";
import * as Climates from "../environment/climates/climates.js";

export default class DungeonGeneratorConfig {
  possibleBiomes: Biome[];
  width: number; // cells
  height: number; // cells
  maxRooms: number;
  minRooms: number;
  minThreatLevel: number;
  maxThreatLevel: number;

  constructor() {
    this.width = 40;
    this.height = 50;
    this.maxRooms = 30;
    this.minRooms = 20;
    this.minThreatLevel = 0;
    this.maxThreatLevel = 5;

    let climate = Climates.random();
    let biomeGenConfig = new BiomeGeneratorConfig(climate);
    let biomeGen = new BiomeGenerator(biomeGenConfig);

    let biomes = [];
    for (let i = 0; i < 5; i++) {
      biomeGen.config.climate = Climates.random();
      biomes.push(biomeGen.generate());
    }
    this.possibleBiomes = biomes;
  }
}
